 }
 .MenuBar{ 
  line-height: 32px; 
  background: #222d32;
  font-size: 14px;
}
.MenuBar a{
  display: block;
  width: 100%;
  color: #ffffff;
  text-align: left;
  padding-left: 40px;
}
.MenuBar .active{
  background: #1e282c;
  color: #ffffff;
  border-left:3px solid #3c8dbc;
}
.MenuBar .on{
 
  color: #ffffff;
}
 .MenuBar>.barTitle>a{ 
  line-height: 32px; 
}
 .MenuBar>.barTitle>.barChild>.barTitle>a.active{
  border-left:none;
  color: #cddce3;
  border-left:3px solid #3c8dbc;
 }
 .barTitleMenu{
   position: relative;
 }
  .barTitleMenu:after{
    width:10px;
    height:10px;
    display: block;
    content: '';
    border-width: 2px;
    border-color:transparent #ffffff #ffffff transparent;
    border-style: solid;
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    position: absolute;
    right:10px;
    top: 50%;
    margin-top: -5px;
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s
 }
  .barTitleMenu.on:after{
    width:10px;
    height:10px;
    display: block;
    content: '';
    border-width: 2px;
    border-color:transparent #ffffff #ffffff transparent;
    border-style: solid;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    position: absolute;
    right:10px;
    top: 50%;
    margin-top: -5px;
 }
